/**
 * Get all tools for a specific category
 */
export function getToolsByCategory(category: ToolCategory): Tool[] {
    return tools.filter((tool) => tool.category === category);
}

/**
 * Get a single tool by slug
 */
export function getToolBySlug(slug: string): Tool | undefined {
    return tools.find((tool) => tool.slug === slug);
}

/**
 * Get all unique categories with their tools count
 */
export function getCategoriesWithCount(): { category: ToolCategory; count: number }[] {
    const categories = Object.keys(categoryInfo) as ToolCategory[];
    return categories.map((category) => ({
        category,
        count: tools.filter((t) => t.category === category).length,
    }));
}

/**
 * Search tools by name or description
 */
export function searchTools(query: string): Tool[] {
    const normalizedQuery = query.toLowerCase().trim();
    if (!normalizedQuery) return tools;

    return tools.filter(
        (tool) =>
            tool.name.toLowerCase().includes(normalizedQuery) ||
            tool.shortDescription.toLowerCase().includes(normalizedQuery) ||
            tool.keywords.some((k) => k.toLowerCase().includes(normalizedQuery))
    );
}
